import React from 'react'

function Image({
    image,
    removeImage

}) {
  return (
    <div className="image">
        
        <div>
            <img src={image} className="img-fluid" width="92px" height="125px"/>
        </div>
        <button onClick={removeImage} value={image} style={{cursor:'pointer',fontSize:'12px'}}>REMOVE</button>
        
        
    </div>
  )
}

export default Image;
